import {Link} from 'react-router-dom';
import './styles.css';


const Navbar = (props) => {
	
	return (
		<div className="navbar">
			<Link to="/misc-feed/" className="link link_1">Misc<sup>&reg;</sup></Link>
			<ul>
				<li><Link to="/misc-feed/game" className="link link_2">Game</Link></li>
				<li><Link to="/misc-feed/calculator" className="link link_3">Calculator</Link></li>
				<li><Link to="/misc-feed/feed" className="link link_4">Feed</Link></li>
			</ul>
		</div>
	);
};

export default Navbar;