import React from 'react';

import Square from './Square/Square';
import './styles.css';


class Board extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        squares: Array(9).fill(null),
        xIsNext: true,
      };
    }
  
    handleClick(i) {
      const squares =
    this.state.squares.slice();
      if (squares[i] || calculateWinner(squares)) {
        return;
      }
      squares[i] = (this.state.xIsNext)?'X':'O';
      this.setState({
        squares: squares,
        xIsNext: !this.state.xIsNext
      });
  
    }
  
    renderSquare(i) {
      return (
        <Square
          value={this.state.squares[i]}
          onClick={() => this.handleClick(i)}
        />
      );
    }
  
    render() {
      const winner = calculateWinner(this.state.squares);
      let status;
      const resetBoard = () => {
        this.setState({xIsNext:this.state.xIsNext,squares:[]});
      }
      if (winner) {
        status = 'Winner: '+winner;
      }
      else if (!this.state.squares.includes(null) && this.state.squares.length===9) {
          status = 'Match drawn';
          // setTimeout(()=>this.setState({squares: Array(9).fill(null)}),3000);
      }
      else {
        status = `Next player: ${(this.state.xIsNext)?'X':'O'}`;
      }
  
      return (
        <div className="game">
          <div className="status">{status}</div>
          <div className="board-row">
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
          </div>
          <div className="board-row">
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
          </div>
          <div className="board-row">
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
          </div>
          <div className="reset-btn">
          <button onClick={resetBoard}>Reset</button>
        </div>
        </div>
      );
    }
  }

  function calculateWinner(squares) {
    const lines = [
      [0,1,2],
      [3,4,5],
      [6,7,8],
      [0,4,8],
      [2,4,6],
      [0,3,6],
      [1,4,7],
      [2,5,8]
    ];
  
    for (let i=0; i < lines.length; i++) {
      const [a,b,c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return squares[a];
      }
    }
    return null;
  }

  export default Board;